.NavbarItems{
    background: linear-gradient(90deg, rgb(163, 12, 250) 0%, rgb(192, 107, 241) 100%);
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    box-shadow: rgba(50, 50, 93, 0.14902) 0px 1px 3px,
    rgba(0, 0, 0, 0.0196078) 0px 1px 0px;
  border: 0;
  outline: 0;
  border-radius: 4px;
  background: #CBC3E3;
}
.navbar-logo{
    justify-self: start;
    margin-left: 0px;
    cursor: pointer;
    color: black;
}
.nav-menu{
    display: grid;
    grid-template-columns: repeat(3, auto);
    grid-gap: 60px;
    list-style: none;
    text-align: center;
    width: 70vw;
    justify-content: end;
    margin-right: 1rem;
}
.nav-menu-h{
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-gap: 60px;
  list-style: none;
  text-align: center;
  width: 70vw;
  justify-content: start;
  margin-right: 1rem;
}
.dropdown {
    position: absolute;
    top: 58px;
    width: 170px;
    transform: translateX(-45%);
    background-color: var(--bg);
    border: var(--border);
    border-radius: var(--border-radius);
    padding: 1rem;
    z-index: 1;
    overflow: hidden;
    transition: height var(--speed) ease;
  }
  :root {
    --bg:  #CBC3E3;
    --bg-accent: #CBC3E3;
    --text-color: #dadce1;
    --nav-size: 60px;
    --border: 1px solid #CBC3E3;
    --border-radius: 8px;
    --speed: 500ms; 
  }
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  a {
    
    text-decoration: none;;
  }
  .menu-item {
    height: 50px;
    display: flex;
    align-items: center;
    border-radius: var(--border-radius);
    transition: background var(--speed);
    padding: 0.5rem;
  }
  
  .menu-item .icon-button {
    margin-right: 0.5rem;
  }
  
  
  .menu-item .icon-button:hover {
    filter: none;
  }
  
  .menu-item:hover {
    background-color: #CBC3E3;
  }
  
  .icon-right {
    margin-left: auto;
  }
  
  /* CSSTransition classes  */
  .menu-primary-enter {
    position: absolute;
    transform: translateX(-110%);
  }
  .menu-primary-enter-active {
    transform: translateX(0%);
    transition: all var(--speed) ease;
  }
  .menu-primary-exit {
    position: absolute;
  }
  .menu-primary-exit-active {
    transform: translateX(-110%);
    transition: all var(--speed) ease;
  }
  
  
  .menu-secondary-enter {
    transform: translateX(110%);
  }
  .menu-secondary-enter-active {
    transform: translateX(0%);
    transition: all var(--speed) ease;
  }
  .menu-secondary-exit {
  
  }
  .menu-secondary-exit-active {
    transform: translateX(110%);
    transition: all var(--speed) ease;
  }